import React, { Component } from 'react';
import closeIcon from './res/close.svg';
import MuffinView from './projects/Muffin.js';
import ForzaView from './projects/Forza.js';
import MasterThesisView from './projects/MasterThesis.js';
import muffinImage from './res/muffin.jpg';
import hockeyImage from './res/hockey.jpg';
import forzaImage from './res/Forza.png';

class Projects extends Component {

    constructor(props) {
        super(props);
        this.state = { view: null };
        this.closeView = this.closeView.bind(this);
    }

    closeView() {
        this.setState({ view: null })
    }

    openView(view) {
        this.setState({ view: view }, window.scrollTo(0, 0))
    }

    render() {
        return (
            <React.Fragment>
                <div className="projects">
                    <div className="portfolio-items">
                        <div className="portfolio-item" onClick={() => this.openView(MasterThesisView)}>
                            <img src={hockeyImage} />
                            <div className="portfolio-item__description">
                                <h3>Second screen application</h3>
                                <p>UI/UX</p>
                            </div>
                        </div>
                        <div className="portfolio-item" onClick={() => this.openView(MuffinView)}>
                            <img src={muffinImage} />
                            <div className="portfolio-item__description">
                                <h3>Tangible neural network project</h3>
                                <p>Physical installation</p>
                            </div>
                        </div>
                        <div className="portfolio-item" onClick={() => this.openView(ForzaView)}>
                            <img src={forzaImage} />
                            <div className="portfolio-item__description">
                                <h3>Forza Football</h3>
                                <p>UI/UX</p>
                            </div>
                        </div>
                    </div>
                </div>
                <PortfolioView view={this.state.view} closeView={this.closeView} />
            </React.Fragment>
        );
    }
}

class PortfolioView extends Component {

    constructor(props) {
        super(props);
        this.escFunction = this.escFunction.bind(this);
        this.handleClick = this.handleClick.bind(this);
    }

    escFunction(event) {
        if (event.keyCode === 27) {
            this.props.closeView()
        }
    }

    handleClick(event) {
        if (event.target.className === 'background') {
            this.props.closeView()
        }
    }

    componentDidMount() {
        document.addEventListener("keydown", this.escFunction, false);
        document.addEventListener('mousedown', this.handleClick);
    }

    componentWillUnmount() {
        document.removeEventListener("keydown", this.escFunction, false);
        document.removeEventListener('mousedown', this.handleClick);
    }

    getView() {
        if (this.props.view === null) {
            return null;
        } else {
            return (
                <div className="background">
                    <div className="window">
                        <img className="close-icon" src={closeIcon} onClick={this.props.closeView} />
                        <div className="content">
                            <this.props.view />
                        </div>
                    </div>
                </div>
            )
        }
    }

    render() {
        return (
            this.getView()
        )
    }
}

export default Projects;